import React, { Component } from 'react';
import jsPDF from 'jspdf';

export default class OrderGenerator extends Component {
    componentDidMount() {
        // this.onClickVC();
    }
    onClickVC = (event) => {
        event.preventDefault();
        let date = this.props.data.date;
        let OrderNo = this.props.data.OrderNo.toString();
        let email = this.props.data.email;
        let phone = this.props.data.phoneNumber;
        let totalProducts = this.props.data.totalProducts.toString();
        let titlePosition = 100;

        //console.log('---New VC---',name,designation,Skills);
        var doc = new jsPDF(
            {
             orientation: 'p',
             unit: 'mm',
             format: 'a4',
             putOnlyUsedFonts:true
            }
            );//'p', 'mm', [210, 297] Portrait format // 'l', 'mm', credit-card lanscape format
        var imgData = 'data:image/jpg;base64,';


        doc.addImage(imgData, 'jpg', 0, 0,210,297 ); 
        //'p', 'mm', [210, 297] Portrait format ,0, 0,90,50 card formate
       // 'l', 'mm', credit-card lanscape format
        doc.setFont("muli");
        doc.setFontSize(16);
        //doc.setTextColor(13,15,49);
        //doc.text(110, 140, 'Mr./Ms. '); left right , top bottom
        doc.setTextColor(0,0,0);
        doc.text(`${date}(MM/DD/YY)`, 16, 53 ); 
        
        doc.setFont("muli");
        doc.setFontSize(16);
        doc.setTextColor(0,0,0);
        doc.text(`#${OrderNo}`, 78, 53  );

        doc.setFont("muli");
        doc.setFontSize(16);
        doc.setTextColor(0,0,0);
        doc.text(`Email-:${email}`, 17, 77 );

        doc.setFont("muli");
        doc.setFontSize(16);
        doc.setTextColor(0,0,0);
        doc.text(`Phone-:${phone}`,  17, 85 );

        
        doc.setFont("muli");
        doc.setFontSize(16);
        doc.setTextColor(13,15,49);
        doc.text(17, 93, 'Total Products: ');
        doc.setTextColor(0,0,0);
        doc.text(totalProducts,  53, 93 );

       
        var a = 140;
        doc.setFont("muli");
        doc.setTextColor(0,0,0);
        for(let i = 0 ; i<this.props.cartCtx.items.length ; i++){
            doc.text(this.props.cartCtx.items[i].title.slice(0,15-3)+'...',  17, a );
            doc.text(this.props.cartCtx.items[i].price.toString() ,  65, a );
            doc.text(`* ${this.props.cartCtx.items[i].amount.toString()}` ,  85, a );
            doc.text((this.props.cartCtx.items[i].price * this.props.cartCtx.items[i].amount).toString(),  100, a );
            a = a + 10;
        }

        doc.setFont("muli");
        doc.setFontSize(12);
        doc.setTextColor(0,0,0);
        doc.text(`${this.props.cartCtx.totalAmount.toFixed(2)}`,  165,178);
        doc.text(`${this.props.cartCtx.totalAmount.toFixed(2)}`,  165,189);
       
    

        doc.setDrawColor(0,0,255);
    //	doc.rect(105, 132, 180, 24, '0'); */
        
        // Save the PDF
        doc.save(`${email}_certi.pdf`);
    }
    
        render() {
            return (
                 <button type="submit" onClick={this.onClickVC} className="btn btn-primary invoice">Invoice &nbsp;&nbsp;&nbsp; <i className="fa fa-download" aria-hidden="true"></i></button>
                
            )
        }
    }
